import ErrorMessage from "../errorMessage/ErrorMessage";
import { Link } from "react-router-dom";
import { Helmet } from "react-helmet";

const Page404 = () => {
    return (
        <div>
            <Helmet>
                <meta
                    name="description"
                    content="Error page"
                />
                <title>Error</title>
            </Helmet>
            <ErrorMessage/>
            <p style={{
                'textAlign': 'center',
                'fontSize': 30,
                'fontWeight': 'bold'
            }}>Page dosen't exist</p>
            <Link style={{
                'display': 'block',
                'textAlign': 'center',
                'fontSize': 30,
                'fontWeight': 'bold',
                'color': '#9f0013'
            }} to='/'>Back to main page</Link>
        </div>
    )
}

export default Page404;